<template>
  <div class="detailNote">
    <h2>{{ myNote.title }}</h2>
    <div class="msg">
      <span>作者名{{ myNote.author_name }}</span
      ><span>2023-1-12发布</span><span>观看量{{ myNote.visit }}</span>
    </div>
    <div class="container">{{ myNote.content }}</div>
    <div class="imgListBox">
      <img :src="item" alt="" v-for="(item,i) in imgList" :key="i">
    </div>
    <div class="last">
      <div class="author">
        <div class="avatar">
          <img
            :src="src"
            alt=""
          />
        </div>
        <div class="sexBox">{{ changeSexType(myNote.author_sex )}}</div>
        <div>{{ myNote.author_name }}</div>
      </div>
      <div class="support">
        {{ myNote.approve }}
        <svg
          :class="isSupport == 1 ?  'icon active' : 'icon'"
          aria-hidden="true" @click="updateSupport"
        >
          <use xlink:href="#icon-dianzan"></use>
        </svg>
       
        <svg
          class="icon"
          :class="isSupport == -1 ? 'active' : ''"
          aria-hidden="true" @click="updateAgainst"
        >
          <use xlink:href="#icon-002cai-1"></use>
        </svg>
         {{ myNote.against }}
      </div>
    </div>
    <div class="comment">
      <div class="commentMsg">
        <input type="text" placeholder="留下你想说的话吧" v-model="comment">
        <button @click="sendComment">提交</button>
      </div>
      <div class="commentList">
         <div class="commentItem" v-for="(item,i) in commentList" :key="i">
        <div class="userMsg">
          <img :src="baseUrl+item.usericon" alt="">
          <span>{{item.username}}</span>
           <div class="commentContent">{{item.content}}</div>
        </div>
       
        <div class="commentItemRight">
          <span>{{item.creare_time}}</span>
          <button @click="deleteComment(item.comment_identity,item.note_identity,i)">删除评论</button>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getNoteInfo } from "@/request/api/home";
import { mapState } from "vuex";
import {votedNote,createComment,getNoteCommentList,deleteNoteComment} from '@/request/api/home'
export default {
  name: "detailNote",
  props: ["note","test"],
  data() {
    return {
      //0不赞 不踩   1点赞  -1踩
      isSupport: 0,
      myNote: this.note,
      imgList: [],
      src:'',
      urls: "",
      imgListBox:[
     
     ],
     //输入框评论的内容
     comment:"",
     token:'',
     //文章id
     note_identity:"",
     commentList:[

     ]
    };
  },
  methods: {
    //更新点赞
   async updateSupport(){
      let token=localStorage.getItem('token')
      this.myNote.approve=this.myNote.approve*1
      let old=""
      //增加点赞
    if(this.isSupport!=1){
         old=this.isSupport
         if(this.isSupport==-1){
          this.myNote.against=this.myNote.against*1-1
         }
        this.isSupport=1
        //更新页面的数量
        this.myNote.approve+=1
       }else{
         old=this.isSupport
        this.isSupport=0
        //更新页面的数量
          this.myNote.approve-=1
       }
       let data={
          flag:this.isSupport+'',
          note_identity:this.note_identity+'',
          voted:old+''
        }
    let res= await votedNote(data,token)
    console.log(res);
    },
    //更新踩
   async updateAgainst(){
      let token=localStorage.getItem('token')
       this.myNote.against= this.myNote.against*1
    let old=''
       if(this.isSupport!=-1){
         old=this.isSupport
         if(old==1){
          this.myNote.approve=this.myNote.approve*1-1
         }
        this.isSupport=-1
        this.myNote.against+=1
       }else{
        old=this.isSupport
        this.isSupport=0
        this.myNote.against-=1
       }
       let data={
         flag:this.isSupport+'',
        note_identity:this.note_identity+'',
          voted:old+''
       }
        let res= await votedNote(data,token)

    },
    //添加评论
   async sendComment(){
      if(this.comment=="")return alert('评论不能为空')
       let data={
        content:this.comment,
        note_identity:this.note_identity+''
       }
        console.log(data);
       let res =await createComment(data,this.token)
       console.log(res);
       this.comment=""
       this.commentList.push(res.data.data)
    },
    //删除评论
   async deleteComment(comment,note,i){
      if(!window.confirm('确认删除吗')) return
     this.commentList.splice(i,1)
      let data={
        comment_identity:comment+"",
        note_identity:note+"",
      }
      let res=await deleteNoteComment(data,this.token)
      console.log(res);
     
   },
   //改变性别的类型
   changeSexType(sex){
    if(sex==1){
      return '男'
    }else if(sex==0){
       return "保密"
    }else{
      return '女'
    }
   }
  },
  computed: {
    ...mapState(["baseUrl"]),
  },
  async mounted() {
     let token = localStorage.getItem("token");
    this.token=token
    console.log('note的内容是',this.note);
    let data={}
    if(this.test){
       this.myNote=this.note
       console.log('mynote的内容啥',this.myNote);
        data={
        note_identity:this.myNote.note_identity
       }
       
    }else{
      data = {
      note_identity: this.myNote.note_basic.identity,
    };
    }
     //获取帖子详细信息
    let res = await getNoteInfo(data, token);
    console.log(res);
    this.myNote = res.data.data;
    this.isSupport = this.myNote.isvoted;
    this.note_identity=this.myNote.note_identity
    this.urls = this.myNote.urls;
    this.src=this.baseUrl+this.myNote.author_icon
    let arr = this.urls.split(",");
    let arr2=[]
    arr.forEach((item) => {
      arr2.push(this.baseUrl + item);
    });
    this.imgList=arr2
    //获取评论列表
    let data2={
     note_identity:this.note_identity
    }
   let res2 =await getNoteCommentList(data2,this.token)
  //  console.log(res2);
   this.commentList=res2.data.data.commentInfo
   console.log(this.commentList);

    
  },
};
</script>

<style lang="less">
.detailNote {
  // background-color: skyblue;
  height: 682px;
  	// overflow-y:scroll;
    overflow: auto;
   .imgListBox{
    display: flex;
     width: 100%;
    flex-wrap: wrap;
    // justify-content: center;
    
      img {
         height: 200px;
         border: 1px solid #666;
      }
   }
  h2 {
    text-align: center;
  }
  .msg {
    text-align: center;
    font-size: 12px;
    color: #666;
    span {
      margin: 0 2px;
    }
  }
  .container {
    padding: 6px;
  }
  .last {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    border: 1px solid #666;
    .author {
      display: flex;
      align-items: center;
      .sexBox{
        margin-right: 4px;
        color: #666;
      }
      .avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: pink;
        overflow: hidden;
        img {
          width: 100%;
        }
      }
    }
    .support {
      display: flex;
      align-items: center;
      .icon {
        margin-left: 10px;
      }
      .active {
        fill: pink;
      }
    }
  }

// 评论区
.comment {
      padding: 10px;
  .commentMsg{
    //  width: 100%;
    //  background-color: skyblue;
     display: flex;
     justify-content: center;
     align-items: center;
     input {
      width: 60%;
      height: 30px;
     }
     button{
      width: 15%;
      border-radius: 0;
      box-sizing: border-box;
      height: 30px;
     }
  }
   .commentList {
        .commentItem {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 5px 0;
          // border: 1px solid #666;
          .userMsg {
            display: flex;
            align-items: center;
             img {
              width: 50px;
              height: 50px;
              border-radius: 50%;

             }
             span{
              color: #666;
             }
             .commentContent{
              margin-left: 10px;
             }
          }
          .commentItemRight{
            display: flex;
            flex-direction: column;
            justify-content: center;
          }
        }
   }
}


}
</style>