<template>
  <div class="register">
    <h2>欢迎你的加入</h2>
    <div class="user">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-yonghu"></use>
      </svg>
      <input type="text" v-model="username" placeholder="用户名"/>
    </div>
    <div class="password">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
      <input type="password" v-model="password" placeholder="密码"/>
    </div>
    <div class="repassword">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
      <input type="password" v-model="repassword" placeholder="确认密码"/>
    </div>
    <div class="">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-youxiang1"></use>
      </svg>
      <input type="text" v-model="email" placeholder="邮箱" />
    </div>
    <div class="setCodeBtn">
      <button @click="getCode">发验证码</button>
    </div>
    <div class="code">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-24gl-envelopeOpen2"></use>
      </svg>
      <input type="text" v-model="code" placeholder="验证码" />
    </div>
    <div class="type">
      <button class="loginBtn" @click="getRegister">注册</button>
      <button class="registerBtn" @click="getLogin">已有账号</button>
    </div>
    <!-- <div class="lostPassword">
       <a href="javascript:;">忘记密码</a>
     </div> -->
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { registerPost, sendEmailCodePost } from "@/request/api/home";
import { verifyRegister } from "@/schema/user";
export default {
  data() {
    return {
      username: "",
      password: "",
      repassword: "",
      email: "",
      haveCode: false,
      code: "",
    };
  },
  computed: {
    ...mapState(["userStatus"]),
  },
  methods: {
    ...mapMutations(["updateUserStatus"]),
       //获取邮箱验证码
    async getCode() {
      alert("发送邮箱验证码中");
      // this.haveCode = true;
      let data={
       email :this.email
      }
      let res = await sendEmailCodePost(data);
      console.log(res);
    },
    //注册
    async getRegister() {
      let data = {
        username: this.username,
        password: this.password,
        repassword: this.repassword,
        email: this.email,
        code:this.code
      };
      let msg = verifyRegister(data);
      if (msg != "") return alert(msg);
      let res = await registerPost(data);
      console.log(res);
      //待优化
     let token= res.data.data.token
     localStorage.setItem('token',token)
     //跳转到登录界面
     this.getLogin()
    },

    //登录
    getLogin() {
      this.updateUserStatus(0);
    },
 
  },
  mounted() {},
};
</script>

<style lang="less">
.register {
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  div {
    //   width: 200px;
    display: flex;
    align-items: center;
    width: 300px;
    height: 30px;
    // background-color: #fff;
    margin: 10px 0;
  }
  .setCodeBtn{
    height: 10px;
  }
  input {
    padding-left: 4px;
    width: 250px;
    height: 30px;
    border: 1px solid #c0c4cc;
  }

  .type {
    box-sizing: content-box;
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    .loginBtn {
      box-sizing: content-box;
      padding: 4px 15px;
      border: 0px;
      background-color: #008cf0;
      color: #fff;
      border-radius: 5px;
    }
    .registerBtn {
      padding: 4px 15px;
      border: 0px;
      background-color: #eaeaea;
      color: #666;
      border-radius: 5px;
    }
  }
  .lostPassword {
    text-align: center;
  }
  input:focus {
    /* border-color: #f07b00; */
    border-color: #008cf0;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
  }
}
</style>