<template>
   <div>
      <div v-if="!isDetail">
        <div class="type" >
        <button @click="getNoteByHot" class="hotBtn">按热度</button>
        <button @click="getNoteByTime" class="timeBtn">按时间</button>
        </div>  
        <div class="noteList">
            <div class="noteItem" v-for="(item,i) in noteList" :key="i">
               <h2>{{item.title}}</h2>
               <div class="time">{{item.create_time}}</div>
               <div class="noteContent">{{item.content}}</div>
               <div><button @click="changIsDetail(i)">查看全文</button></div>
            </div>
        </div>

      </div>


    <!-- 显示详情 -->
     <div v-if="isDetail">
        <div><svg class="icon" aria-hidden="true" @click="changIsDetail">
        <use xlink:href="#icon-fanhui"></use>
      </svg></div>
         <div><detailNote :note="noteList[nowI]" :test="true"></detailNote></div>
     </div>






   </div>
</template>

<script>
import detailNote from '@/components/detailNote.vue'
import {searchNotesScoreOrTime} from '@/request/api/home'
export default {
    components:{
     detailNote
    },
   data() {
    return {
        noteList:[],
        isDetail:false,
        nowI:-1,
    }
   },
   methods:{
    changIsDetail(i){
        this.nowI=i
        console.log(i);
       this.isDetail=!this.isDetail
    },
    async getNoteByHot(){
        let data={
            type:1+''
        }
     let res= await searchNotesScoreOrTime(data)
     this.noteList=res.data.data.noteInfo
     console.log(this.noteList);
     },
    async getNoteByTime(){
          let data={
            type:2+''
        } 
         let res= await searchNotesScoreOrTime(data)
         this.noteList=res.data.data.noteInfo
         console.log(this.noteList);
     }
   }
}
</script>

<style>
  .type {
    display: flex;
    justify-content: center;
  
  }
  .hotBtn{
    border-radius: 0;
    background-color: #ee610adf;
    color: #fff;
     border-radius: 45px 0 0 0;
    box-sizing: content-box;
    padding: 6px 15px;
  }
  .timeBtn{
   border-radius: 0;
       padding: 6px 15px;
       border-radius: 0 45px 0 0;
  }
  .noteList{
    /* background-color: pink; */
    padding: 0 10px;
    /* overflow-y: scroll; */
    overflow: auto;
    height: 680px;
  }
  .noteList .noteItem {
     background-color: #dee1e6;
     height: 150px;
     margin: 10px 0;
  }
  .noteItem h2 {
    text-align: center;

  }
  .noteItem .time {
    color: #61affe;
    text-align: center;
  }
  .noteItem .noteContent{
     white-space: nowrap;
/* 内容会被修剪，并且其余内容是不可见的。 */
overflow: hidden;
/* 显示省略符号来代表被修剪的文本。 */
text-overflow: ellipsis 
  }
</style>