<template>
 <div>
   <div class="retrieve" v-if="!isChangePassword">
    <h2>找回密码</h2>
    <div class="">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-youxiang1"></use>
      </svg>
        <input type="text" v-model="email">
    </div>
    <div>
      <button @click="getCode">获取验证码</button>
    </div>
     <div class="">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-24gl-envelopeOpen"></use>
      </svg>
        <input type="text" v-model="code">
    </div>
    <div class="type">
      <button class="loginBtn" @click="getSend">发送
      </button>
      <button class="registerBtn" @click="getRegister">我是新人</button>
    </div>
    
  </div>
  <div v-if="isChangePassword" class="retrieve">
   <h2>重置密码</h2>
    <div class="">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-yonghu"></use>
      </svg>
        <input type="text" v-model="username" placeholder="用户名">
    </div>
     <div class="">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
        <input type="text" v-model="password" placeholder="密码">
    </div>
    <div class="">
       <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
        <input type="text" v-model="repassword" placeholder="重复密码">
    </div>
    <div class="type">
      <button class="loginBtn" @click="goChangePassword">发送
      </button>
      <button class="registerBtn" @click="getRegister">我是新人</button>
    </div>
  </div>
 </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import {verifyEamil,verifyForgetPassword} from "@/schema/user"
import {sendEmailCodePost,forgetPasswordVerifyEmailCodePost,forgetPasswordModifyPasswordPost} from "@/request/api/home"
export default {
    data() {
        return {
             email:'',
            //是否发送邮箱
            isSend:false,
            code:'',
            isChangePassword:false,
            username:'',
            password:"",
            repassword:"",
        }
    },
    computed:{
        ...mapState(["userStatus"]),
    },
   methods: {
    ...mapMutations(["updateUserStatus"]),
    //修改密码
   async goChangePassword(){
    alert(666)
    let data={
      username:this.username,
      password:this.password,
      repassword:this.repassword
    }
       let res= await forgetPasswordModifyPasswordPost(data)
       console.log(res);
    },
     //注册
    getRegister(){
    this.updateUserStatus(1)
    },
    //获取验证码
 async getCode(){
       let data={
       email :this.email
      }
    
    let msg=verifyEamil(data)
    if(msg!=''){
       return alert(msg)
    }
      let res = await sendEmailCodePost(data);
      console.log(res);
   
    },
    //发送验证
  async getSend(){
     let data={
      email :this.email,
      code:this.code
     }
    let msg =verifyForgetPassword(data)
    if(msg!='')return alert(msg)
    this.isChangePassword=true
   let res =await forgetPasswordVerifyEmailCodePost(data)
   console.log(res);
   //假设成功登录
   //将token保存到本地
   }
   },
   mounted(){
}
};
</script>

<style lang="less">
.retrieve {
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  div {
      //   width: 200px;
    display: flex;
    align-items: center;
    width: 300px;
    height: 30px;
    // background-color: #fff;
    margin: 10px 0;
  }
  input {
    padding-left: 4px;
        width: 250px;
      height: 30px;
      border: 1px solid #c0c4cc;
  }


  .type {
    box-sizing: content-box;
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    .loginBtn {
     padding: 4px 15px;
    border: 0px;
     background-color: #008cf0;
     color: #fff;
     border-radius: 5px;
  }
  .registerBtn{
    padding: 4px 15px;
    border: 0px;
     background-color: #eaeaea;
     color: #666;
      border-radius: 5px;
  }
  }
  .lostPassword{
    text-align: center;
  }
   input:focus {
        /* border-color: #f07b00; */
        border-color: #008cf0;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px
                rgba(0, 0, 0, 0.075),
                #008cf0;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    }
}
</style>