export function verifyLogin(data){
    let msg=""
      if(data.username=="")return (msg="用户名不能为空")
      if(data.password=="")return (msg='密码不能为空')
      if(data.repassword=="")return (msg='重复密码不能为空')
      if(data.password!==data.repassword)return (msg="两次密码不一致")
      return msg
}
export function verifyEamil(data){
    let msg=''
     if(data.email=="")return (msg="邮箱不能为空") 
     return msg
}
export function verifyRegister(data){
    let msg=""
    if(data.username=="")return (msg="用户名不能为空")
      if(data.password=="")return (msg='密码不能为空')
      if(data.repassword=="")return (msg='重复密码不能为空')
      if(data.password!==data.repassword)return (msg="两次密码不一致")
      if(data.email=="")return (msg="邮箱不能为空")
      if(data.code=="")return (msg="验证码不能为空")
      return msg
}
export function verifyForgetPassword(data){
   let msg=""
   if(data.email=="")return (msg="邮箱不能为空")
   if(data.code=="")return (msg="验证码不能为空")
   return msg
   }