<template>
  <div class="article">
    <input type="text" placeholder="标题" v-model="title"/>
    <div class="articleImg">
      <div class="imgList" v-for="(item, i) in imgList" :key="i">
        <img :src="item" alt="" />
           <svg v-show="isdelete" class="icon" aria-hidden="true" @click="deleteImg(i)">
        <use xlink:href="#icon-guanbi1"></use>
      </svg>
      </div>
      <svg class="icon" aria-hidden="true" @click="selectImg">
        <use xlink:href="#icon-image"></use>
      </svg>
      <div></div>
      <input ref="file" class="sendImg" type="file" @change="addImg" />
    </div>
    <textarea name="" id="" cols="30" rows="10" v-model="content" placeholder="请输入内容"></textarea>

    <button @click="goModifyNote">提交</button>
  </div>
</template>

<script>
import {verifyArticle} from "@/schema/article"
import {createNote,uploadLocalPost,modifyNote} from "@/request/api/home" 
import { mapState } from 'vuex';
export default {
  props:["note"],
  data() {
    return {
      //图片列表(用于展示)
      imgList: [],
      //未拼接的路劲（用于发送请求）
      srcList: [],
      //所有未拼接路径的字符串（请求参数）
       urls:'',
      content:"",
      title:"",
      //判断数据是否回来，回来时就删除
      isdelete:false,
     
      token:'',
       myNote:'',
       //文章id
       note_identity:""
    };
  },
  computed:{
    ...mapState(["baseUrl"])
  },
  methods: {
    //删除图片
    deleteImg(i){
        //删除指定下标图片
        this.imgList.splice(i,1)
        //删除存放src的列表
        thi.srcList.splice(i,1)
    },
    //发送文章请求
       async send() {
        if(!this.isdelete)return alert('网络有点慢，稍后再试')

     this.urls= this.srcList.join(',')
    console.log(this.urls);
     let token=this.token
     let  data={
        title:this.title,
        content:this.content,
        urls:this.urls
      }
      console.log(data);
      let msg=verifyArticle(data)
      if(msg!='')return alert(msg)
      
     let res = await createNote(data,token)
     console.log(res);
      //发布帖子成功
     
    },
    //点击时
    selectImg() {
      this.$refs.file.click();
    },
    //改变时,更新图片到服务器
   async addImg() {
      let file = this.$refs.file.files[0];
      console.log(this.$refs.file.files);
      this.imgList.push(URL.createObjectURL(file));
      //发送请求。。。。。
      let token=this.token
      let data={
           type:'2',
           file:file
      }
      this.isdelete=false
      //发送更新图片请求
     let res=await uploadLocalPost(data,token)
     console.log(res);
     if(res.data.code==1000){
        this.isdelete=true,
        //未拼接的路劲
        this.srcList.push(res.data.data.url)
        console.log(this.srcList);

     }

    },
    //修改帖子
   async goModifyNote(){
    this.urls=this.srcList.join(',')
      let data={
        content:this.content,
        note_identity:this.note_identity,
        title:this.title,
        urls:this.urls
      }
      console.log(data);
       let res = await modifyNote(data,this.token)
       console.log(res);
        if(res.data.code==1000){
        alert('修改成功')
        this.content=""
         this.title=""
         this.imgList=[],
         this.urls=''
      }
    }
  },
  mounted(){
    this.token=localStorage.getItem('token')
    this.myNote=this.note
    console.log("修改文章",this.myNote);
     this.title=this.myNote.title
     this.content=this.myNote.content
     this.note_identity=this.myNote.identity
     this.urls=this.myNote.urls
     this.srcList=this.urls.split(",")
     console.log("this.srcList",this.srcList);
     this.srcList.forEach(item=>{
     this.imgList.push(this.baseUrl+item) 
     })
     console.log(this.imgList);
     this.isdelete=true
  }
};
</script>
<style lang="less" scoped>
.article {
  padding: 10px;
  .articleImg {
       height: 100px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    .sendImg {
      display: none;
    }
    .imgList {
      width: 100px;
      height: 100px;
      overflow: hidden;
      position: relative;
      // background-color: pink;
      border: 1px solid greenyellow;
      .icon {
        position: absolute;
        top: 0;
        right: 0;
        fill: red;
      }
      img {
        position: absolute;
        height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
  input {
    margin: 20px 0;
    width: 100%;
    height: 30px;
    border: 1px solid #c0c4cc;
  }
  textarea {
    width: 100%;
    border: 1px solid #c0c4cc;
  }
  input:focus {
    border-color: #008cf0;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
  }
  textarea:focus {
    border-color: #008cf0;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
  }
}
</style>