<template>
  <div class="login">
    <h2>登录</h2>
    <div class="user">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-yonghu"></use>
      </svg>
      <input type="text" v-model="username" placeholder="用户名" />
    </div>

    <div class="password">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
      <input type="password" v-model="password"  placeholder="密码"/>
    </div>
    <div class="repassword">
        <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-suo"></use>
      </svg>
        <input type="password" v-model="repassword" placeholder="确认密码"/>
    </div>
    <div class="type">
      <button class="loginBtn" @click="getLogin">登录</button>
      <button class="registerBtn" @click="getRegister">我是新人</button>
    </div>
     <!-- <div class="lostPassword">
       <a @click="test" href="javascript:;">忘记密码</a>
     </div> -->
  </div>
</template>

<script>
import {setLogin} from '@/request/api/home'
import { mapMutations, mapState } from 'vuex';
import {verifyLogin} from "@/schema/user"
export default {
    data() {
        return {
          
            username:'',
            password:'',
            repassword:'',
        }
    },
    computed:{
         ...mapState(["userStatus"]),
    },
   methods: {
    ...mapMutations(["updateUserStatus","updateUserInfo"]),
    //登录
     async getLogin(){
        console.log(this.username);
          let data={
           username: this.username,
           password:this.password,
           repassword:this.repassword,
          }
        
        let msg= verifyLogin(data)
        if(msg!='')return alert(msg)
       let res =  await setLogin(data)
       console.log(res);
      //  以 token 判断是否登录成功
       if(res.data.data.token){
           let token =res.data.data.token
           localStorage.setItem('token',token)
           let userInfo=res.data.data.userInfo
           this.updateUserInfo(userInfo)
           this.updateUserStatus(3)
       }else{
        alert(res.data.msg)
       }
      },
      //注册
    getRegister(){
       this.updateUserStatus(1)
    },
    test(){
console.log(this.userStatus); 
   }
   },
   mounted(){
     //获取token值
     let token =localStorage.getItem('token')
     if(token){
       this.updateUserStatus(3)
     }
}
};
</script>

<style lang="less" scoped>
.login {
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  div {
    //   width: 200px;
    display: flex;
    align-items: center;
    width: 300px;
    height: 30px;
    // background-color: #fff;
    margin: 10px 0;
  }
  input {
     padding-left: 4px;
      width: 250px;
     height: 30px;
      border: 1px solid #c0c4cc;
  }


  .type {
    box-sizing: content-box;
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    .loginBtn {
     padding: 4px 15px;
    border: 0px;
     background-color: #008cf0;
     color: #fff;
     border-radius: 5px;
  }
  .registerBtn{
    padding: 4px 15px;
    border: 0px;
     background-color: #eaeaea;
     color: #666;
      border-radius: 5px;
  }
  }
  .lostPassword{
    text-align: center;
  }
   input:focus {
        /* border-color: #f07b00; */
        border-color: #008cf0;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px
                rgba(0, 0, 0, 0.075),
                #008cf0;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    }
}
</style>