<template>
  <div>
    <div v-show="isShowDetail">
      <svg class="icon" aria-hidden="true" @click="changeIsShowDetail">
        <use xlink:href="#icon-fanhui"></use>
      </svg>
    </div>
    <div v-if="!isShowDetail" class="notes">
      <div class="search">
        <input type="text" placeholder="请输入关键字" v-model="keyword" />
        <button @click="sendKeyword">搜索</button>
      </div>
            <div class="tip" v-show="showNoNote">没有找到相关的帖子哦</div>
      <!-- 循环文章 -->
      <div class="noteList" v-if="!showNoNote">
        <div class="noteItemBox" v-for="(item, i) in noteList" :key="i">
          <h2>{{ item.title }}</h2>
          <div class="time">{{ item.create_time }}</div>
          <div class="noteContent">{{ item.content }}</div>
          <!-- <h2>标题</h2>
          <div class="time">时间</div>
          <div class="noteContent">内容</div> -->
          <button @click="changeIsShowDetail(i)">阅读全文</button>
        </div>
      </div>

    </div>

    <div v-if="isShowDetail"><detailNote :note="noteList[nowI]" :test="true"></detailNote></div>
  </div>
</template>

<script>
import { searchNotes } from "@/request/api/home";
import detailNote from "@/components/detailNote.vue";
import noteItem from "@/components/noteItem.vue";
export default {
  components: {
    detailNote,
    noteItem,
  },
  data() {
    return {
      keyword: "",
      noteList: [],
      isShowDetail: false,
      showNoNote:false,
      //判断用户点击的是哪一个文章
      nowI:-1
    };
  },
  methods: {
    //是否查看详细
    changeIsShowDetail(i) {
      if(!localStorage.getItem('token')){
          return alert('请先登录！')
      }
      console.log(i);
      console.log(this.noteList[i]);
      this.nowI=i
      this.isShowDetail = !this.isShowDetail;
    },
    async sendKeyword() {
      let token = localStorage.getItem("token");
      if (this.keyword == "") return alert("请输入关键字");
      let data = {
        // page: "1",
        // size: "1",
        keyword: this.keyword,
      };
      this.keyword=""
      let res = await searchNotes(data);
      console.log(res);
      if (res.data.code == 1000) {
        this.noteList = res.data.data.noteInfo;
        console.log(this.noteList);
        console.log(this.noteList.length);
        if(this.noteList.length==0){
         this.showNoNote=true
        }else{
          this.showNoNote=false
        }
        
      }
    },
  },
};
</script>

<style lang="less">
.notes {
  position: relative;
  height: 89vh;
  .tip{
    text-align: center;
    font-weight: 700;
    color: #008cf0;
  }
  .noteList {
    padding: 0 10px;
    height: 626px;
    // overflow-y: scroll;
    overflow: auto;
    .noteItemBox {
      height: 150px;
      margin: 10px 0;
      // background-color: pink;
      background-color: #dee1e6;
      h2 {
        text-align: center;
      }
      .time {
        text-align: center;
        color: #008cf0;
      }
      .noteContent {
        //超出一行省略号
        white-space: nowrap; //禁止换行
        overflow: hidden;
        text-overflow: ellipsis; //...
      }
      button {
        margin-top: 20px;
      }
    }
  }
  .search {
    border-radius: 10px;
    margin: 30px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 60%;
    input {
      border: 1px solid #e8e8ed;
      height: 100%;
      width: 80%;
    }
    input:focus {
      /* border-color: #f07b00; */
      border-color: #008cf0;
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), #008cf0;
    }
    button {
      padding: 0;
      height: 100%;
      width: 20%;
      border-radius: 0;
    }
    .icon {
      width: 20px;
      height: 20px;
      fill: #fff;
    }
  }

  .list {
    position: absolute;
    width: 100%;
    display: flex;
    // position: fixed;
    // bottom: 30px;
    bottom: 10px;
    background-color: pink;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      height: 100%;
      width: 30px;
      line-height: 30px;
      text-align: center;
      border: 1px solid #666;
      margin: 0 2px;
    }
  }
}
</style>