<template>
  <div>
    <div class="myNotes" v-show="(!isDetail&&!isModify)">
    <div class="noteMsg" v-show="myNotesTotal != 0">
      <div class="showTotal">您一共写了{{ myNotesTotal }}篇文章!</div>


       <div class="notelist">
         <!-- 循环文章 -->
      <div class="note" v-for="(item, i) in noteList" :key="i">
         <div class="left">
           <h2>{{ item.note_basic.title }}</h2>
           <div class="time">{{changeTimeType(item.CreatedAt)}}</div>

           <!-- 撞名了content -->
        <div class="noteContent">{{ item.note_basic.content }}</div>
        </div>
           <div class="right">
            <button @click="changeIsDetail(i)">阅读全文</button>
            <button class="modifyBtn" @click="changeisModify">修改文章</button>
            <button class="deleteBtn" @click="goDeleteNote(item.note_identity,i)">删除文章</button>
          </div>
      </div>
       </div>
    </div>
    <div class="tip" v-show="myNotesTotal == 0">您还没有写过文章呢~赶紧来一篇吧！</div>

  </div>
   <div class="detail" v-if="isDetail">
        <div>
          <svg class="icon" aria-hidden="true" @click="changeIsDetail">
        <use xlink:href="#icon-fanhui"></use>
      </svg>
        </div>
        <detailNote :note="noteList[nowI]"></detailNote>
      </div>
      <div v-if="isModify">
         <svg class="icon" aria-hidden="true" @click="changeisModify">
        <use xlink:href="#icon-fanhui"></use>
      </svg>
        <modifyNote :note="noteList[nowI].note_basic"></modifyNote>
      </div>
  </div>
</template>

<script>
import { getNoteList } from "@/request/api/home";
import { mapState } from "vuex";
import detailNote from "@/components/detailNote.vue";
import modifyNote from '@/components/modifyNote.vue'
import {deleteNote}from '@/request/api/home'
export default {
  components: {
    detailNote,
    modifyNote
  },
  data() {
    return {
      //是否展示详细帖子信息
      isDetail: false,
      //是否修改帖子
      isModify:false,
      token: "",
      nowI:0,
      noteList: [
        {
          note_basic: {
            title: "1111",
            content: "sdfdsfdasfafa",
          },
        },
        {
          note_basic: {
            title: "2222",
            content: "sdfdsfdasfafa",
          },
        },
      
        
      ],
      myNotesTotal: 4,
      
    };
  },
  computed: {
    ...mapState(["baseUrl"]),
  },
  methods: {
    changeTimeType(data){
      let arr=data.split("T")
      let result= arr[0]
      let str=arr[1].split(".")[0]
      result=result+"/"+str
      return  result
    },
    //删除帖子
   async goDeleteNote(data,i){
      if(!window.confirm('确认删除吗')) return
         console.log(data,i);
       let token=localStorage.getItem('token')
       let msg ={
         note_identity:data
       }
  
     let res=await deleteNote(msg,token)
    
     console.log(res);
      this.noteList.splice(i,1)

    },
    changeisModify(){
       this.isModify=!this.isModify
    },
    changeIsDetail(i) {
      if(typeof i === 'number'){
        this.nowI=i
      }else{
        this.nowI=0
      }
      this.isDetail=!this.isDetail
    },
  },
  async mounted() {
    this.token = localStorage.getItem("token");
    let data = {
      page: "1",
      size: "10",
    };
    let token = this.token;
    let res = await getNoteList(data, token);
    console.log(res);
    //获取个人帖子
    if (res.data.code == 1000) {
      let result = res.data.data;
      this.myNotesTotal = result.count;
      this.noteList = result.noteInfo;
      this.noteList.reverse()
    }
    console.log("noteList",this.noteList);
  },
};
</script>

<style>
.showTotal{
  color: #008cf0;
  text-align: center;
  font-weight: 700;
}
.notelist{
  height: 695px;
  /* overflow-y: scroll; */
  overflow: auto;
}
.note {
  background-color: #dee1e6;
  margin: 10px;
  padding: 10px;
    display: flex;
  justify-content: space-between;

}
.time {
  color: #008cf0;
  text-align: center;
}
.tip {
  margin-top: 200px;
  text-align: center;
}
.note h2 {
  text-align: center;
}
.left {
  width: 70%;
}
.noteContent{
  width: 100%;
  white-space: nowrap;
/* 内容会被修剪，并且其余内容是不可见的。 */
overflow: hidden;
/* 显示省略符号来代表被修剪的文本。 */
text-overflow: ellipsis 
}
.right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

}
.right button {
  margin: 2px;
}
 .deleteBtn {
  background-color: #ee0a24;
}
.modifyBtn {
  background-color: #ff976a;
}
.detail {
  width: 100%;
}
@media only screen and (max-width: 850px) {
  .note{
    height: 100px;
  }
}
</style>