<template>
  <div class="userMsg">
    <div v-show="!isChange">
      <div class="avatar">
        <img :src="url" alt="" />
      </div>
      <div class="username">您好！{{ username }}</div>
      <div><span>年龄：{{age}}</span></div>
      <div><span>性别：{{judgeSex(sex)}}</span></div>
       
      <div class="changeBtn"><button @click="changeBtn">修改用户信息</button></div>
         <div><a href="javascript:;" @click="logOut">退出登录</a></div>
    </div>
    <!-- 修改时的界面 -->
    <div class="changeMsg" v-show="isChange">
      <div class="changeAvatar">
        <img :src="url" alt=""/>
        <input class="selectAvatar"
          ref="avatar"
          type="file"
          @change="changeAvatar"
        />
       <div class="selectIcon" @click="goSelectAvartar">
         <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-jia"></use>
      </svg>
       </div>
      </div>

      <div><span>年龄</span><input type="text" v-model="age" /></div>
      <div>
        <span>性别</span>
        保密<input type="radio" v-model="sex" name="sex" value="0" />
        男<input type="radio" v-model="sex" name="sex" value="1" />
        女<input type="radio" v-model="sex" name="sex" value="2" />
      </div>
      <div><span> 用户名</span><input type="text" v-model="username" /></div>
      <div class="changeBox"><button @click="changeUserInfo">修改用户信息</button>
       <button class="unmodifyBtn" @click="unmodify">取消修改</button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  userGetUserInfoGet,
  userModifyUserInfoPost,
  uploadLocalPost,
} from "@/request/api/home";
import { mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      token: "",
      username: "www",
      age: "18",
      sex: "0",
      url:'',
      isChange: false,
      usericon:''
    };
  },
  methods: {
  
    ...mapMutations(["updateUserStatus","updateUserInfo"]),
    judgeSex(sex){
       if(sex==0) return "保密"
       else if(sex==1)return "男"
       else return "女"
    },
    goSelectAvartar(){
       this.$refs.avatar.click()
    },
    //取消修改用户信息 
    unmodify(){
      this.isChange=false
    },
    //退出登录
    logOut() {
      
      // 弹窗确认退出
  if(!window.confirm('确认退出吗')) return
  console.log("退出测试");
      this.updateUserStatus(0);
      localStorage.clear("token");
    },
    //修改按钮
    changeBtn() {
      this.isChange = true;
    },
    //修改头像
    async changeAvatar() {
      console.log("改变");
      console.log(this.$refs.avatar.files);
      let token = localStorage.getItem("token");
      var fileInfo = this.$refs.avatar.files[0];
      console.log(fileInfo);
      let data = {
        type: "1",
        file: fileInfo,
      };
      let res = await uploadLocalPost(data, token);
      console.log(res);
      //对图片的src进行处理
      let newUrl=res.data.data.url
      //保存未拼接的路径
      this.usericon=newUrl
       this.url=this.baseUrl+newUrl
        console.log(this.url);
      
    },
    //修改用户信息
    async changeUserInfo() {
      let data = {
        username: this.username,
        age: this.age+"",
        sex: this.sex+'',
        url: this.usericon,
      };
      let token = this.token;
      console.log(data);
      let res = await userModifyUserInfoPost(data, token);
      console.log(res);
      let userInfo={
        username: this.username,
        age: this.age,
        sex: this.sex,
        src: this.url,
      }
        this.updateUserInfo(userInfo)
      //修改用户信息成功
      this.isChange=false
    },
  },
  computed:{
      ...mapState(["baseUrl","userInfo"])
  },
  //获取用户信息
  async mounted() {
    this.token = localStorage.getItem("token");
    let token = this.token;
    let res = await userGetUserInfoGet(token);
    console.log(res);
    //对数据进行处理
    let userInfo=res.data.data
    this.age=userInfo.age
    this.sex=userInfo.sex
    this.url=this.baseUrl +userInfo.usericon
    console.log(this.url);
     this.username = userInfo.username
     console.log(userInfo,this.url);
     let user={
        src:this.url,
        username:userInfo.username
     }
    this.updateUserInfo(user)
   
    

   
  },
};
</script>

<style lang="less" scoped>
.userMsg {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .avatar {
    width: 100px;
    height: 100px;
    background-color: skyblue;
    border-radius: 50%;
    img {
        width: 100%;
        height: 100%;
    }
  }
  .changeBtn{
    margin: 5px 0;
  }
  
  .changeMsg {
    span {
      display: inline-block;
      width: 100px;
    }

   .changeAvatar {
    display: flex;
     img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background-color: pink;
    }
    .selectAvatar{
      display: none;
    }
    .selectIcon {
      display: flex;
      width: 100px;
      height: 100px;
     justify-content: center;
     align-items: center;
     border: 1px solid #666;
}
   }
   .changeBox {
    margin-top: 20px;
    button {
      box-sizing: content-box;
      background-color: #008cf0;
      padding: 5px 10px;
      border-style: none;
      color: #fff;
      border-radius: 7px;
    }
    .unmodifyBtn{
      margin-left: 5px;
      background-color: #fc5531;
    }
   }
  }
}
</style>