<template>
    <div class="user">
     <div v-show="userStatus<=2">
       <login v-show="userStatus==0"></login>
      <register v-show="userStatus==1"></register>
       <retrieve v-show="userStatus==2"></retrieve>
      <div class="forgetPassword"><a href="#" @click="forgetPassword" v-show="userStatus!=2">忘记密码</a></div>
     </div>
     <div v-if="userStatus==3">
        <userMsg></userMsg>
     </div>
    </div>
</template>

<script>
import login from '@/components/login/login.vue'
import register from '@/components/login/register.vue'
import retrieve from '@/components/login/retrieve.vue'
import userMsg from '@/components/login/userMsg.vue'
import { mapMutations, mapState } from 'vuex'
export default {
  components:{
    login,
    register,
    retrieve,
    userMsg
  },
  data() {
    return {
    
    }
  },
  methods:{
    ...mapMutations(["updateUserStatus"]),
    forgetPassword(){
     this.updateUserStatus(2)
 
    }
  },
  computed:{
     ...mapState(["userStatus"])
  },
  mounted(){
    console.log(this.userStatus);
  }
}
</script>

<style lang="less">
    .user {
      .forgetPassword{
        text-align: center;
      }
    }
</style>